* {
  box-sizing: border-box;
  margin: 0;
  border: 0;
}

@media (prefers-reduced-motion: no-preference) {
  .world-container {
    animation: rotate infinite 20s linear;
  }
  .rainbow {
    animation: us infinite 5s linear;
  }
}

@keyframes rotate {
  from {
    transform: perspective(900px) rotateX(70deg) rotate(0deg);
  }
  to {
    transform: perspective(900px) rotateX(70deg) rotate(360deg);
  }
}

