body {
  background-color: #000522;
  overflow: hidden;
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
}

.world-container {
  height: 100vh;
  width: 100vh;
  position: relative;
  transform: perspective(900px) rotateX(70deg) rotate(360deg);
}

.earth {
  height: 100%;
  width: 100%;
  background-repeat: no-repeat;
  background-position: right top;
  background-size: contain;
  background-image: url("./earth.png");
}

.rainbow {
  height: 60px;
  width: 60px;
  background-color: yellow;
  position: absolute;
  top: 40%;
  z-index: 2;
}
